<template>
  <div class="category-body" ref="toTop">
    <nav-header :leftArrow="!$route.query.browerFirstIn">
      <div v-if="shareShow"  class="share-btn" slot="right" :style="{backgroundImage: `url(${$globalIcon.blackShareIcon})`}" @click="doShare"></div>
    </nav-header>
    <div style="height: .92rem"></div>
    <skeleton :nums="4" :iheight="'.4rem'" v-if="isSkeletonLoading"/>
    <empty v-if="showEmpty"/>
    <div v-if="!showEmpty && !isSkeletonLoading">
      <div class="content-row" ref="contentRow">
        <div class="top-bg">
          <div class="title-row">
            <img class="symbol-img" v-lazy="'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/rankList/icon_start_left.png'"/>
            <div class="title-text">
              {{categoryName}}热销排行榜
            </div>
            <img class="symbol-img" v-lazy="'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/rankList/icon_start_right.png'"/>
          </div>
          <div class="title-des">
            公证数据 · 新鲜热门 · 每日更新
          </div>
        </div>
        <div class="prod-list">
          <product-item v-for="(item, index) in prodList" :index="index" :key="item.sku + index" :prodInfo="item"/>
        </div>
        <more-rank :cateList="cateList"/>
        <div class="no-more">没有更多啦~</div>
      </div>
      <!-- 返回顶部 -->
    <top-feedBack :isShow="showToTopFlag" @hiddenFeedBack="hiddenFeedBack" @toTop="fnScrollToTop"></top-feedBack>
    </div>
  </div>
</template>
<script>
import ProductItem from './components/productItem';
import MoreRank from './components/moreRank';
import NavHeader from '@/components/header';
import Skeleton from '@/components/skeleton';
import empty from '../components/empty';
import { mapGetters } from 'vuex';
import mixins from '@/components/top-feedBack/mixins.js'
import { getHotSaleProducts, getProdcutPrice, getCateList } from '@/http/service/rankList';
const localData = {
  scrollTop: 0,
};
export default {
  name: 'rankList',
  mixins: [mixins],
  components: {
    NavHeader,
    ProductItem,
    MoreRank,
    Skeleton,
    empty,
  },
  data() {
    return {
      isSkeletonLoading: true,
      shareShow: this.$util.isUplus() || this.$util.isSgRn(),
      categoryName: '',
      prodList: [],
      cateList: [],
      showEmpty: false,
    };
  },
  computed: {
    ...mapGetters(['mId']),
    ...mapGetters({
      location: 'getlocationInfo',
    }),
    getShareId() {
      return sessionStorage.getItem('shareUserId') || '';
    },
  },
  watch: {
    location(newVal) {
      this.getTopProds();
    }
  },
  activated() {
    this.getTopProds();
    this.getCateList();
    this.$nextTick(() => {
      document.querySelector('.app-warp').scrollTo(0, localData.scrollTop || 0)
    });
  },
  beforeRouteLeave (to, from, next) {
    localData.scrollTop = document.querySelector('.app-warp').scrollTop;
    next();
  },
  mounted() {
    this.categoryName = this.$route.query.categoryName;
    this.getTopProds();
    this.getCateList();
  },
  methods: {
    doShare() {
      let url = location.href.indexOf('?') !== -1 ? location.href + '&shareId=' + this.mId : location.href + '?shareId=' + this.mId
      this.$util.inAppShare({
        url,
      })
    },
    getTopProds() {
      getHotSaleProducts({
        streetId: this.location.townshipId,
        cityId: this.location.cityId,
        regionId: this.location.districtId,
        provinceId: this.location.provinceId,
        shareId: this.getShareId,
        productCateIds: this.$route.query.categoryId ? this.$route.query.categoryId.split(',') : [],
      }).then(({ data }) => {
        if (data.success) {
          if (data.data.sgHotProducts.length < 3) {
            this.isSkeletonLoading = false;
            this.showEmpty = true;
          } else {
            getProdcutPrice({ traceId: data.data.traceId }).then(priceList => {
              this.isSkeletonLoading = false;
              if (priceList.data.success) {
                data.data.sgHotProducts.map(item => {
                  item.sellPoint = !!item.sellPoint && item.sellPoint.join(' | ');
                  priceList.data.products.map(_item => {
                    if (item.sku === _item.sku) {
                      item.finalPrice = _item.finalPrice.toFixed(2)
                      item.commission = _item.commission
                    }
                  });
                });
                this.prodList = data.data.sgHotProducts;
                console.log('prodList:', this.prodList);
              } else {
                this.showEmpty = true;
              }
            });
          }
        } else {
          this.isSkeletonLoading = false;
          this.showEmpty = true;
        }
      });
    },
    getCateList() {
      getCateList({ parentId: 0 }).then(({ data }) => {
        let catesList = [];
        data.data.map(item => {
          let catesId = [];
          if (item.url && item.url.includes('productCateId=')) {
            catesId = item.url.replace('productCateId=', '').split(';');
            catesList.push({
              ...item,
              catesId
            });
          }
        });
        catesList = catesList.filter(item => item.catesId.join() !== this.$route.query.categoryId);
        const promiseList = [];
        catesList.map(item => {
          promiseList.push(getHotSaleProducts({
            streetId: this.location.townshipId,
            cityId: this.location.cityId,
            regionId: this.location.districtId,
            provinceId: this.location.provinceId,
            shareId: this.getShareId,
            productCateIds: item.catesId,
          }))
        });
        Promise.all([...promiseList]).then((result) => {
          let filterCateList = [];
          result.map(({ data }, index) => {
            if (data.data.sgHotProducts && data.data.sgHotProducts.length > 2) {
              filterCateList.push(catesList[index]);
            }
          })
          this.cateList = [...filterCateList].slice(0, 6);
        });
      });
    },
  },
}
</script>
