<template>
  <div class="product-container" v-if="prodInfo">
    <img class="prod-img" v-lazy="prodInfo.image"/>
    <div class="detail-row">
      <div class="title">
        {{prodInfo.productFullName}}
      </div>
      <div class="sub-title">
        {{prodInfo.sellPoint}}
      </div>
      <div class="hot-row">
        热销指数{{prodInfo.saleScore}}
      </div>
      <div class="final-price">
        ￥<span class="price-int">{{prodInfo.finalPrice.split('.')[0]}}</span>.<span style="font-size:.24rem;">{{prodInfo.finalPrice.split('.')[1]}}</span>
      </div>
      <div class="button" @click="toDetail">
        去购买
      </div>
    </div>
    <img class="img-number-tag" v-lazy="hotImg[index]" v-if="index < 3"/>
    <div class="number-tag" v-else>
      {{index + 1}}
    </div>
  </div>
</template>
<script>
export default {
  name: 'productItem',
  props: {
    index: {
      type: Number,
      default: 0,
    },
    prodInfo: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      hotImg: [
        'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/rankList/icon_top_hot_one.png',
        'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/rankList/icon_top_hot_two.png',
        'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/rankList/icon_top_hot_three.png',
      ],
    };
  },
  methods: {
    toDetail() {
      this.$util.gioTrackWithVariable('MB15613', {
        productId_var: this.prodInfo.productId + ''
      })
      this.$router.push(`/goodsDetail?productId=${this.prodInfo.productId}`);
    }
  },
}
</script>
